@import "./common/mixins",
"common/variables",
"common/global";

.growtype-carousel-wrapper {
  .alignfull {
    margin-left: calc(-50vw + 50%);
    margin-right: calc(-50vw + 50%);
    max-width: none;
  }

  &[data-overflow="initial"] {
    .slick-list {
      overflow: initial;
    }
  }

  &.has-arrows {
    .slick-slider {
      padding-left: 15px;
      padding-right: 15px;
    }

    .growtype-carousel {
      max-width: calc(100% - 60px);
      margin: auto;
    }
  }

  &[data-dots="false"] {
    .slick-dots {
      display: none;
    }
  }

  .slick-dots {
    li {
      width: initial;
      height: initial;

      button[data-show-dot-label="true"] {
        width: initial;
        height: initial;
        font-size: 14px;
        color: black;
        opacity: 0.5;

        &:before {
          content: initial;
        }
      }

      &.slick-active {
        button[data-show-dot-label="true"] {
          opacity: 1;
        }
      }
    }
  }
}

.alignfull .growtype-carousel-wrapper {
  &.has-arrows {
    .growtype-post-container {
      margin-left: 30px;
      width: calc(100% - 60px);
    }
  }
}

.growtype-carousel.slick-slider {
  opacity: 0;

  &.slick-initialized {
    opacity: 1;
  }
}

.growtype-carousel {
  .slick-slider {
    opacity: 0;

    &.slick-initialized {
      opacity: 1;
    }
  }
}
